.container {
    display: flex;
    gap: 16px;
    padding: 15px;
}

.user-elements {
    cursor: pointer;
}

.user-elements div {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(#c82d8e, #f99d4e);
}

.imgUserStory {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    border: 2px solid var(--background);
}

.user-elements span {
    font-size: 13px;
    cursor: pointer;
}