.headerPost {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px;
}

.infosPost {
    display: flex;
    align-items: center;
    gap: 16px;
}

.headerPost .imagePost {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.imgPost img {
    width: 100%;
    object-fit: cover;
}

.footerPost {
    padding: 10px 15px;
}

.engajamento {
    display: flex;
    justify-content: space-between;
}

.engajamento .icons1 {
    display: flex;
    gap: 16px;
}

.icon {
    cursor: pointer;
}

.like {
    margin-top: 6px;
}

.like span {
    font-size: 15px;
    font-weight: 500;
}

.legenda {
    margin: 6px 0 3px;
}

.legenda p strong {
    font-weight: 500;
}

.timePost {
    font-size: 11px;
    color: var(--secondary);
}

.comentario {
    border-top: 1px solid var(--border-secondary);
    margin-top: 8px;
    display: flex;
    justify-content: space-between;
}

.comentarioF {
    padding: 15px 0;
    display: flex;
    align-items: center;
    gap: 16px;
}

.comentarioF input {
    font-size: 16px;
}

.comentario button {
    color: var(--quartary);
    cursor: pointer;
    font-weight: 500;
}
