.MainGrid {
    display: grid;
    grid-template-areas: "firstColumn secondColumn";
    grid-template-columns: 641px 293px;
    max-width: 957px;
    margin: 75px auto;
    grid-gap: 35px;
}

.box {
    background: var(--background);
    border: 1px solid var(--border);
}

.boxSugestao {
    position: fixed;
    flex: 1;
    max-width: 293px;
}