.header {
    background: var(--background);
    border-bottom: 1px solid var(--border);
    position: fixed;
    top: 0;
    width: 100%;
}

.header .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    max-width: 957px;
    margin: 0 auto;
    width: 90%;
}

.header .container .logo {
    width: 110px;
    cursor: pointer;
}

.inputF {
    background: var(--background-search);
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 4px 10px;
}

.inputF input {
    font-size: 14px;
    padding: 0 7px;
}

.menuI {
    display: flex;
    align-items: center;
    gap: 16px;
    cursor: pointer;
}

.menuI .imgUser {
    width: 26px;
    height: 26px;
    border-radius: 50%;
}