.containerSugestao {
    margin-top: 18px;
}

.headerSugestao {
    display: flex;
    align-items: center;
    gap: 20px;
}

.headerSugestao img {
    height: 56px;
    width: 56px;
    border-radius: 100%;
    cursor: pointer;
}

.userInfos {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1;
}

.userInfos .infos span {
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.userInfos .mudar {
    font-weight: 500;
    color: var(--quartary);
    cursor: pointer;
}

.userInfos .infos p {
    font-size: 13px;
    color: var(--secondary);
}

.headerS {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 10px 0;
}

.headerS p {
    font-size: 13px;
    color: var(--secondary);
    font-weight: 500;
}

.headerS span {
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
}

.userSugestao {
    margin-top: 5px;
}

.infosSugestao {
    margin: 15px 0 0 5px;
    display: flex;
    align-items: center;
    gap: 16px;
}

.infosSugestao img {
    width: 36px;
    height: 36px;
    border-radius: 100%;
    cursor: pointer;
}

.infoSugestao {
    flex: 1;
}

.infoSugestao span {
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.infoSugestao p {
    font-size: 13px;
    color: var(--secondary);
    margin-top: 3px;
}

.infosSugestao .seguir {
    font-size: 12px;
    cursor: pointer;
    color: var(--quartary);
    font-weight: 500;
}

.footerSugestao {
    margin-top: 25px;
    flex: 1;
}

.footerSugestao p {
    font-size: 12px;
    color: var(--tertiary);
    margin-bottom: 15px;
    cursor: pointer;
}